import React from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';

import image from './image.jpg'
import propTypes, { defaultProps } from './propTypes';
import styles from './styles';

const BobcatHireCard = ({ classes }) => (
  <Card className={classes.card}>
    <CardMedia
      component="img"
      alt="Bobcat hire"
      className={classes.media}
      height="240"
      image={image}
      title="Bobcat hire"
    />
    <CardContent className={classes.content}>
      <Typography gutterBottom variant="h5" component="h2">
        Bobcat Hire
      </Typography>
      <Typography component="p">
        Bobcat and operator hire for clearing and excavation.
      </Typography>
    </CardContent>
  </Card>
);

BobcatHireCard.propTypes = propTypes;
BobcatHireCard.defaultProps = defaultProps;

export default withStyles(styles)(BobcatHireCard);
